import React from 'react';
import Preloader from '../../common/Preloader/Preloader';
import styles from './profileInfo.module.css';
import avatar from '../../../assets/img/ava.jpg';
import PfofileDescription from './ProfileDescription';
import PopUp from '../../common/PopUp/popUp';
import PopUpMessage from '../../common/PopUp/PopUpMessage'
import PopUpAvatar from '../../common/PopUp/popUpAvatar';

const ProfileInfo = ({profile, isMyPage, isFollowed, sendMessageMode, avatarPopup, setPopupAvatar, followUnfollow, userId, ...props}) => {
    const updatePhoto = (e) => {

        if (e.target.files[0]) {
            props.savePhoto(e.target.files[0])
        }


    }
 
    return profile
        
        ? <div className={styles.profileInfoContainer}>
            <div className={styles.profileInfo}>
                <div>
                    <div className={props.isMyPage ? styles.avatarContainer + ' ' + styles.avatarUpdate : styles.avatarContainer}>
                        <img  onClick={()=>{ profile.photos.large && setPopupAvatar(true)}} className={ styles.avatar + ' ' + (profile.photos.large && styles.avatarCustom)} src={profile.photos.large ? profile.photos.large : avatar} alt="" />
                        {isMyPage 
                            && <div className={styles.changePhoto}>
                                <label for="upload-photo" className={styles.changePhotoLabel}>Update photo</label>
                                <input className={styles.changePhotoInput} id="upload-photo" type='file' onChange={updatePhoto} /> 
                                </div> 
                        }
                        {isMyPage && <div role='button' className={styles.edit} onClick={()=>{props.setEditProfile(true)}}>Edit Profile</div>  }   
                    </div>
                    {!isMyPage && <div role='button' className={styles.send} onClick={()=>{props.setMessageMode(true)}}>Send Message</div>}
                    {!isMyPage && isFollowed
                        ? <div className={styles.send} onClick={()=>{followUnfollow(false, userId)}}> Unfollow </div>
                        : <div className={styles.send} onClick={()=>{followUnfollow(true, userId)}}> Follow </div>
                    }
                    {sendMessageMode && <PopUp user={profile} Component={PopUpMessage} closePopup={() => { props.setMessageMode(false)} } /> }
                    {avatarPopup &&  <PopUp avatar={profile.photos.large ?  profile.photos.large  : avatar}  Component={PopUpAvatar} closePopup={() => { setPopupAvatar(false)} } />}
                </div>
                <PfofileDescription setEditProfile={props.setEditProfile} isEditMode={props.isEditMode}  isMyPage={isMyPage} profile={profile} {...props} />
                
            </div>
        </div>
        : <Preloader />
}

export default ProfileInfo;