import React from 'react';

import { reduxForm, Field } from 'redux-form';
import { createField, Input, Textarea } from '../../common/FormControls/FormControls';
import { required } from '../../../utils/validators/validator';
import { Prompt } from 'react-router'
import SideBar from '../../SideBar/SideBar';
import styles from './AboutProfile.module.css';


const AboutProfile = ({ setEditProfile, isEditMode, profile, isMyPage, saveProfileData }) => {
    const onSubmit = (formData) => {
        saveProfileData(formData);
    }
    return <div className={styles.container}>
        <div className={styles.header}> <div className={styles.aboutMe}>About me</div>{isMyPage && !isEditMode && <div role='button' className={styles.edit} onClick={() => { setEditProfile(true) }}></div>}</div>
        {profile.aboutMe && <div className={styles.row}> <div className={styles.nameInfo}>Description:  </div> <div>{profile.aboutMe}</div></div>}
        <div className={styles.row}><div className={styles.nameInfo}>Looking for a job:</div> <div>{profile.lookingForAJob ? 'yes' : 'no'}</div>   </div>
        {profile.lookingForAJobDescription && <div className={styles.row}> <div className={styles.nameInfo}>Skills:</div> <div>{profile.lookingForAJobDescription}</div>  </div>}
        {Object.keys(profile.contacts).some(i=>profile.contacts[i] != null && profile.contacts[i] != '') && <h2 className={styles.header}>Contacts</h2>}
        {!isEditMode && Object.keys(profile.contacts).map((keys) => {
            if (profile.contacts[keys]) {
                return <div className={styles.row}> 
                        <div className={styles.nameInfo}>{keys + ':'}</div>
                        <div><a href={profile.contacts[keys]} target='_blank'>{profile.contacts[keys].slice(8)}</a></div>
                </div>
            }
        })}
        
        {isMyPage && isEditMode && <ProfileEditorReduxForm isEditMode={isEditMode} initialValues={profile} onSubmit={onSubmit} profile={profile} />}
    </div>

}

const ProfileEditor = ({ handleSubmit, profile, ...props}) => {
    return <form onSubmit={handleSubmit}>
       
        <h3>Full name</h3>
        {createField('Full Name', 'fullName', [required], Input, false, null)}
        <label><h3>Looking for a Job:</h3> </label>
        <div>
            <label><Field name="lookingForAJob" component={'input'} type="radio" value='true' /> Yes</label>
            <label><Field name="lookingForAJob" component={'input'} type="radio" value='false' checked={true} /> No</label>
        </div>
        <h3>Skills:</h3>
        {createField(null, 'lookingForAJobDescription', [required], Textarea, false, null, null, { colls: 10, rows: 30 })}
        <h3>About me:</h3>
        {createField(null, 'aboutMe', [required], Textarea, false, null, null, { colls: 10, rows: 30 })}
        <h2>Contacts:</h2>
        {Object.keys(profile.contacts).map((key) => {
            return <div>
                <h3>
                    {key}
                </h3>
                {createField(key, 'contacts.' + key, null, Input, false, null, { value: profile.contacts[key] })}
            </div>
        })
        }
        <Prompt
            when={props.dirty && SideBar}
            message='You have unsaved changes, are you sure you want to leave?'
        />
        <button  className={styles.save}>save</button>
    </form>
}
const ProfileEditorReduxForm = reduxForm({ form: 'edit-profile' })(ProfileEditor);
export default AboutProfile;