import { getAuthThunk } from "./auth-reducer";


const INITIALIZED_SUCCESS = 'app-reducer/INITIALIZED_SUCCESS';


let initialState = {
    initialized: false
}

const appReducer = (state = initialState, action) => {

    switch (action.type) {
        case INITIALIZED_SUCCESS:
         return {
             ...state,
             initialized: true
         }
        default: return state;

    }

}

export const initiallizedSuccess = () => ({ type: INITIALIZED_SUCCESS });

export const initializeAppThunk = () => (dispatch) => {
    let promise = dispatch(getAuthThunk());
    Promise.all([promise])
    .then(() => {
        dispatch(initiallizedSuccess());
    })
    
}

export default appReducer;